/**
 * Blocks we allow in WP-Markdown.
 *
 * @type {Object}
 */
const allowedBlocks = [
	'core/heading',
	'core/paragraph',
	'core/image',
	'core/list',
	'core/quote',
	'core/separator',
	'core/embed',
	'core/code',
	'core/more',
	'coblocks/gist',
];

export default allowedBlocks;
